.pt-table-cell {
  display: inline-block;
  height: 20px;
  padding: 0 10px;
  line-height: 20px;
  font-size: 12px;
  -webkit-transition: color 300ms;
  -o-transition: color 300ms;
  transition: color 300ms; }
  .pt-dark .pt-table-cell:not([class*="pt-intent-"]):not(.pt-loading) {
    color: #f5f8fa; }
  .pt-table-cell.pt-intent-primary {
    background-color: rgba(19, 124, 189, 0.1);
    color: #137cbd; }
  .pt-dark .pt-table-cell.pt-intent-primary {
    background: rgba(19, 124, 189, 0.1);
    color: #2b95d6; }
  .pt-table-cell.pt-intent-success {
    background-color: rgba(15, 153, 96, 0.1);
    color: #0f9960; }
  .pt-dark .pt-table-cell.pt-intent-success {
    background: rgba(15, 153, 96, 0.1);
    color: #15b371; }
  .pt-table-cell.pt-intent-warning {
    background-color: rgba(217, 130, 43, 0.1);
    color: #d9822b; }
  .pt-dark .pt-table-cell.pt-intent-warning {
    background: rgba(217, 130, 43, 0.1);
    color: #f29d49; }
  .pt-table-cell.pt-intent-danger {
    background-color: rgba(219, 55, 55, 0.1);
    color: #db3737; }
  .pt-dark .pt-table-cell.pt-intent-danger {
    background: rgba(219, 55, 55, 0.1);
    color: #f55656; }
  .pt-table-editing-enabled .pt-table-cell {
    cursor: text; }
  .pt-table-selection-enabled .pt-table-cell {
    cursor: cell; }
  .pt-table-cell.pt-table-truncated-cell {
    overflow: hidden; }
  .pt-table-cell.pt-large,
  .pt-large .pt-table-cell {
    height: 30px;
    line-height: 30px;
    font-size: 14px; }
  .pt-table-cell.pt-loading {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    color: transparent; }
  .pt-table-cell:focus {
    outline: none; }

.pt-table-cell-interactive {
  z-index: 21; }

.pt-table-striped .pt-table-cell-ledger-even {
  background-color: #ffffff; }

.pt-table-striped .pt-table-cell-ledger-odd {
  background-color: #fafcfd; }

.pt-dark .pt-table-striped .pt-table-cell-ledger-even {
  background-color: #293742; }

.pt-dark .pt-table-striped .pt-table-cell-ledger-odd {
  background-color: #2d3c48; }

.pt-table-editable-name input {
  height: 20px; }

.pt-table-editable-text {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 0 10px; }

