.pt-table-overlay-layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 20;
  overflow: hidden;
  pointer-events: none; }

.pt-table-overlay {
  position: absolute; }

.pt-table-region {
  border: 1px solid #8a9ba8;
  background-color: rgba(138, 155, 168, 0.1); }

.pt-table-selection-region {
  border: 1px solid #137cbd;
  background-color: rgba(19, 124, 189, 0.1); }
  .pt-table-column-headers .pt-table-selection-region {
    background-color: transparent; }
  .pt-table-row-headers .pt-table-selection-region {
    background-color: transparent; }

.pt-table-focus-region {
  border: 2px solid #137cbd; }

.pt-table-column-headers .pt-table-region {
  border-bottom: none; }

.pt-table-row-headers .pt-table-region {
  border-right: none; }

.pt-table-vertical-guide {
  top: 0;
  bottom: 0; }

.pt-table-horizontal-guide {
  right: 0;
  left: 0; }

.pt-table-reordering-cursor-overlay {
  cursor: -webkit-grabbing;
  cursor: grabbing; }
  .pt-table-reordering .pt-table-reordering-cursor-overlay {
    pointer-events: all; }

